
























/********* breadcrumb part ***********/
.breadcrumb[data-v-45e02e7a] {
    background: transparent;
    padding: 22px 0;
    border-bottom: 1px solid #e6e6e6;
}
.breadcrumb ul li[data-v-45e02e7a] {
    display: inline-block;
    margin-right: 40px;
    position: relative;
}
.breadcrumb ul li[data-v-45e02e7a]:before {
    position: absolute;
    left: -25px;
    top: 0;
    content: "/";
}
.breadcrumb ul li[data-v-45e02e7a]:first-child:before {
    content: none;
}
.breadcrumb ul li a[data-v-45e02e7a],
.breadcrumb ul li[data-v-45e02e7a] {
    color: #333366;
}
.breadcrumb ul[data-v-45e02e7a],
.breadcrumb[data-v-45e02e7a] {
    margin-bottom: 0;
}
@media (min-width:320px) and (max-width: 568px) {
.breadcrumb ul li[data-v-45e02e7a] {
        display: inline-block;
        margin-right: 15px;
        position: relative;
}
.breadcrumb ul li[data-v-45e02e7a]:before {
        display: none
}
}



