/* RIBBON BADGES */

    /* OPEN RESTAURANT */
.open-ribbon[data-v-44287474] {
  position: absolute;
  z-index: 1;
  overflow: hidden;
  width: 100px;
  height: 100px;
}
.open-ribbon span[data-v-44287474] {
  font-size: 10px;
  font-weight: bold;
  color: #FFF;
  text-transform: uppercase;
  text-align: center;
  line-height: 20px;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  width: 100px;
  display: block;
  background: #79A70A;
  background: -webkit-gradient(linear, left top, left bottom, from(#62C95A), to(#228B22));
  background: linear-gradient(#62C95A 0%, #228B22 100%);
  -webkit-box-shadow: 0 3px 10px -5px rgba(0, 0, 0, 1);
          box-shadow: 0 3px 10px -5px rgba(0, 0, 0, 1);
  position: relative;
  top: 11px;
  left: -30px;
}
.open-ribbon span[data-v-44287474]::before {
  position: absolute;
  left: 0px;
  top: 100%;
  z-index: -1;
  border-left: 3px solid #228B22;
  border-right: 3px solid transparent;
  border-bottom: 3px solid transparent;
  border-top: 3px solid #228B22;
}
.open-ribbon span[data-v-44287474]::after {
  content: "";
  position: absolute; right: 0px; top: 100%;
  z-index: -1;
  border-left: 3px solid transparent;
  border-right: 3px solid #228B22;
  border-bottom: 3px solid transparent;
  border-top: 3px solid #228B22;
}

    /* ./OPEN RESTAURANT */

    /* CLOSED RESTAURANT */
.closed-ribbon[data-v-44287474] {
  position: absolute;
  z-index: 1;
  overflow: hidden;
  width: 100px;
  height: 100px;
}
.closed-ribbon span[data-v-44287474] {
  font-size: 10px;
  font-weight: bold;
  color: #FFF;
  text-transform: uppercase;
  text-align: center;
  line-height: 20px;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  width: 100px;
  display: block;
  background: -webkit-gradient(linear, left top, left bottom, from(#F70505), to(#B22222));
  background: linear-gradient(#F70505 0%, #B22222 100%);
  -webkit-box-shadow: 0 3px 10px -5px rgba(0, 0, 0, 1);
          box-shadow: 0 3px 10px -5px rgba(0, 0, 0, 1);
  position: relative;
  top: 11px;
  left: -31px;
}
.closed-ribbon span[data-v-44287474]::before {
  content: "";
  position: absolute; left: 0px; top: 100%;
  z-index: -1;
  border-left: 3px solid #B22222;
  border-right: 3px solid transparent;
  border-bottom: 3px solid transparent;
  border-top: 3px solid #B22222;
}
.closed-ribbon span[data-v-44287474]::after {
  content: "";
  position: absolute; right: 0px; top: 100%;
  z-index: -1;
  border-left: 3px solid transparent;
  border-right: 3px solid #B22222;
  border-bottom: 3px solid transparent;
  border-top: 3px solid #B22222;
}

