/* ==========================
*   Fonts
============================*/

@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:200,300,400,600,700|Give+You+Glory);
@import url(https://fonts.googleapis.com/css?family=Raleway);
@import url(https://fonts.googleapis.com/css?family=Oswald);
@import url(https://fonts.googleapis.com/css?family=Montserrat);

/* ==========================
*   Typography
============================*/

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Source Sans Pro", sans-serif;
  color: #25282b;
}

a {
  color: #ffb42d;
}

a:hover {
  text-decoration: none;
  color: #82b21c;
}

.lead {
  font-size: 1.25rem;
  font-weight: 400;
  color: #404048;
}

ul {
  list-style-type: none;
  padding: 0;
}

hr {
  margin-top: 1rem;
  margin-bottom: 1rem;
  border: 0;
  border-top: 1px solid #eaebeb;
}

/*==================================
* Bootstrap grid tweaks
*===================================*/

.container {
  max-width: 1270px;
  width: 94%;
}

/* Remove space from columns */

.row.no-gutter {
  margin-left: 0 !important;
  margin-right: 0 !important;
}

.row.no-gutter [class*="col-"]:not(:first-child),
.row.no-gutter [class*="col-"]:not(:last-child) {
  padding-right: 0 !important;
  padding-left: 0 !important;
}

/*==================================
* Just simple warppers
*===================================*/

.page-wrapper {
  padding-top: 68px;
  /* Used for inpper pages if navigation fixed to the top */
}

.inner-page {
  margin-top: 30px;
  margin-bottom: 50px;
}

/*==================================
* Foms
*===================================*/

.custom-control-input:checked ~ .custom-control-indicator {
  color: #fff;
  background-color: #ffb42d;
  margin-top: 0;
  padding: 10px;
  border-radius: 100%;
}

.custom-checkbox .custom-control-indicator {
  border-radius: 100%;
}

.custom-control-indicator {
  padding: 10px;
  background-color: #fff;
  box-shadow: 0 0 1px 3px rgba(93, 92, 99, 0.05);
  border: 1px solid rgba(116, 135, 150, 0.23);
}

.custom-control-input:focus~.custom-control-indicator {
  -webkit-box-shadow: 0 0 0 0.075rem #fff, 0 0 0 0.2rem #ffb42d;
  box-shadow: 0 0 0 0.075rem #fff, 0 0 0 0.2rem #ffb42d;
}

.custom-control-input:active ~ .custom-control-indicator {
  color: #fff;
  background-color: #ffb42d;
}

.custom-control-input:disabled ~ .custom-control-indicator {
  cursor: not-allowed;
  background-color: #eee;
}

.custom-control-input:disabled ~ .custom-control-description {
  color: #767676;
}

.custom-control {
  padding-left: 30px;
}

.form-control {
  border: 1px solid #eaebeb;
  border-radius: 2px;
}

.custom-select {
  line-height: 1;
}

/* ==========================
*   Buttons
============================*/

.form-control-lg,
.dropdown-menu,
.btn,
.btn-group-lg > .btn,
.btn-lg,
.img-rounded {
  border-radius: 2px;
}

.btn {
  transition: all 0.4s;
}

.btn-secondary {
  color: #373a3c;
  background-color: rgba(252, 251, 249, 0.68);
  border-color: #eaebeb;
}

.btn-theme {
  background-color: #ffb42d;
  border-color: #ffb42d;
  color: #fff;
}

.btn-theme-dash {
  border: 2px dashed #ffb42d;
  background-color: transparent;
  color: #ffb42d;
}

.btn-theme-dash:hover,
.btn-theme,
.btn-theme.btn-lg:hover,
.btn-secondary:hover {
  background-color: #ffb42d;
  color: #fff;
  border: 1px solid #ffb42d;
}

.btn-theme-dash:hover {
  border: 2px solid #ffb42d;
  color: #fff;
}

.bg-white {
  background-color: #fff;
}

/* ==========================
*   Helpers
============================*/

.space-xs {
  padding-bottom: 15px;
  padding-top: 15px;
}

.space-sm {
  padding-bottom: 30;
  padding-top: 30px;
}

.space-md {
  padding-top: 90px;
  padding-bottom: 90px;
}

.m-b-10 {
  margin-bottom: 10px;
}

.m-b-20 {
  margin-bottom: 20px;
}

.m-b-30 {
  margin-bottom: 30px;
}

.m-t-10 {
  margin-top: 10px;
}

.m-t-20 {
  margin-top: 20px;
}

.m-t-30 {
  margin-top: 30px;
}

/* .b-l {
  border-left: 1px solid #edeed;
}

.b-r {
  border-left: 1px solid #edeed;
}

.b-t {
  border-top: 1px solid #edeed;
}

.b-b {
  border-bottom: 1px solid #edeed;
} */

.b-b-0 {
  border-bottom: none !important;
}

.b-t-0 {
  border-top: none !important;
}

.b-l-0 {
  border-left: none !important;
}

.b-r-0 {
  border-right: none !important;
}

.align-wrap {
  display: inline-table;
  min-height: 100%;
}

.align-vertical {
  display: table-cell;
  vertical-align: middle;
}

.font-white {
  color: #fff;
}

/* ============================
* PAGINATION
==============================*/

.page-item.disabled .page-link,
.page-item.disabled .page-link:focus,
.page-item.disabled .page-link:hover {
  border-color: #eaebeb;
}

.page-link {
  color: inherit;
  background-color: #fff;
  border: 1px solid #eaebeb;
}

.page-item.active .page-link,
.page-item.active .page-link:focus,
.page-item.active .page-link:hover {
  background-color: #ffb42d;
  border-color: #ffb42d;
}

.menu-widget,
.widget {
    border: 1px solid #eaebeb;
    background: #fafaf8;
    border-radius: 2px;
    position: relative;
}

.widget-body {
    padding: 20px;
}

.panel {
    margin-bottom: 15px;
    border: 1px solid #dcdee3;
    border-radius: 3px;
    box-shadow: none;
}

.panel-heading {
    padding: 15px;
    border: none!important;
    border-radius: 0;
}

.panel-heading i {
    padding-right: 7px;
    font-size: 14px
}

.panel-title {
    font-size: 16px;
    font-weight: 600;
    margin-top: 0;
    margin-bottom: 0;
    color: #25282b;
}

.panel-title a {
    color: #25282b;
}

.panel .panel-body {
    padding: 20px;
}

.widget-delivery .col-xs-6 {
  padding: 25px 15px 20px;
  background: #fff;
  border-right: 1px solid #eaebeb;
  display: -webkit-inline-box;
  border-bottom: 1px solid #eaebeb;
}

.widget-delivery .col-xs-6:last-of-type {
  border-right: 1px solid transparent;
}

.sidebar .widget-delivery .col-xs-6 {
  border-bottom: none
}

/* TABS DO RESTAURANTE */

.nav-pills {
  text-align: center;
  list-style: none;
  display: table;
  table-layout: fixed;
  width: 100%;
}

.nav-pills > .nav-item {
  display: table-cell;
}

.nav-item > .nav-link {
  display: block;
  font-size: 16px;
  font-family: 'Montserrat', sans-serif;
}

/* Responsividade */

@media (min-width: 426px) and (max-width: 681px) {
  .nav-item > .nav-link {
    font-size: 15px;
  }
}

/* Responsividade */

@media (min-width: 320px) and (max-width: 426px) {
  .nav-item > .nav-link {
    font-size: 15px;
  }
}

/* ./TABS DO RESTAURANTE */


