    /* OPEN RESTAURANT */
.open-ribbon[data-v-023ef968] {
  position: absolute;
  z-index: 1;
  overflow: hidden;
  width: 100px;
  height: 100px;
}
.open-ribbon span[data-v-023ef968] {
  font-size: 10px;
  font-weight: bold;
  color: #FFF;
  text-transform: uppercase;
  text-align: center;
  line-height: 20px;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  width: 100px;
  display: block;
  background: #79A70A;
  background: -webkit-gradient(linear, left top, left bottom, from(#62C95A), to(#228B22));
  background: linear-gradient(#62C95A 0%, #228B22 100%);
  -webkit-box-shadow: 0 3px 10px -5px rgba(0, 0, 0, 1);
          box-shadow: 0 3px 10px -5px rgba(0, 0, 0, 1);
  position: relative;
  top: 11px;
  left: -30px;
}
.open-ribbon span[data-v-023ef968]::before {
  position: absolute;
  left: 0px;
  top: 100%;
  z-index: -1;
  border-left: 3px solid #228B22;
  border-right: 3px solid transparent;
  border-bottom: 3px solid transparent;
  border-top: 3px solid #228B22;
}
.open-ribbon span[data-v-023ef968]::after {
  content: "";
  position: absolute; right: 0px; top: 100%;
  z-index: -1;
  border-left: 3px solid transparent;
  border-right: 3px solid #228B22;
  border-bottom: 3px solid transparent;
  border-top: 3px solid #228B22;
}

