





















































/* ==========================
*   Hero
============================*/
.bg-image[data-v-6f47d7b6] {
  background: url(../../../assets/img/home/banner/banner-1670x680.jpg) no-repeat center center;
  background-size: cover;
}
.hero[data-v-6f47d7b6] {
  padding-top: 10%;
  padding-bottom: 5%;
  text-align: center;
  position: relative;
}
.hero h1[data-v-6f47d7b6] {
  font-family: "Give You Glory", cursive;
  color: #fff;
  font-size: 3.6em;
  font-weight: 700;
}
.hero[data-v-6f47d7b6]:before {
  content: "";
  display: block;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
  background-color: rgba(7, 6, 29, 0.37);
}
.hero .hero-inner[data-v-6f47d7b6] {
  position: relative;
  z-index: 1;
}

/* ==========================
*   Hero steps
============================*/
.step-item[data-v-6f47d7b6] {
  display: inline-block;
  margin: 0 40px 0;
  position: relative;
}
.banner-form[data-v-6f47d7b6] {
  margin: 0 auto 30px;
}
.step-item img[data-v-6f47d7b6] {
  display: block;
  margin: 0 auto 15px;
}
.step-item h4[data-v-6f47d7b6] {
  font-family: "Give You Glory", cursive;
  color: #fff;
  font-size: 21px;
}
.step-item h4 span[data-v-6f47d7b6] {
  color: #ffb42d;
}
.step1[data-v-6f47d7b6]:after,
.step2[data-v-6f47d7b6]:after {
  position: absolute;
  content: "";
  right: -58%;
  top: -20%;
  width: 130px;
  height: 32px;
  background: url("../../../assets/img/home/banner/arrow.png");
}
.step2[data-v-6f47d7b6]:after {
  background: url("../../../assets/img/home/banner/arrow-dotted.png");
  height: 25px;
  right: -55%;
  /* top: -5%; */
}
@media (max-width: 568px) and (min-width: 280px) {
.step1[data-v-6f47d7b6]:after,
  .step2[data-v-6f47d7b6]:after {
    display: none;
}
.hero h1[data-v-6f47d7b6] {
    font-size: 2.2em;
    padding-top: 22%;
}
.step-item[data-v-6f47d7b6] {
    margin: 0 30px 10px;
}
}
@media (min-width: 768px) and (max-width: 991px) {
.step-item[data-v-6f47d7b6] {
    margin: 0 30px 0;
}
.hero h1[data-v-6f47d7b6] {
    font-size: 3em;
    padding-top: 5%;
}
}



