






















































/*-----------------------------------  
How it works section
-----------------------------------*/
.how-it-works[data-v-0a95724c] {
    background-image: url(../../../assets/img/home/city.png), url(../../../assets/img/home/pattern.png);
    background-repeat: repeat-x, repeat;
    background-position: bottom, center;
    padding: 90px 0;
    position: relative;
}
.how-it-works[data-v-0a95724c]:after {
    position: absolute;
    right: 20px;
    top: -30px;
    background: url(../../../assets/img/home/peanuts.png);
    width: 150px;
    height: 51px;
    content: "";
}
.how-it-works .text-sm-center>h2[data-v-0a95724c] {
    color: #d8bea5;
    text-align: center;
    margin-bottom: 50px;
    font-family: 'Raleway', sans-serif;
}
.how-it-works-steps[data-v-0a95724c] {
    position: relative;
    padding: 0 2.1rem;
}
.how-it-works .how-middle[data-v-0a95724c] {
    margin-top: 70px;
}
.how-it-works p.pay-info[data-v-0a95724c] {
    font-size: 16px;
    color: #d8bea5;
    margin-top: 25px;
    text-align: center;
    font-family: 'Raleway', sans-serif;
}
.how-it-works h3[data-v-0a95724c] {
    color: #fff;
    font-family: 'Raleway', sans-serif;
}


/*-----------------------------------  
Steps
-----------------------------------*/
.how-it-works p[data-v-0a95724c] {
    color: #9c9da5;
}
.step .icon[data-v-0a95724c] {
    position: relative;
    width: 86px;
    height: 86px;
    margin: 10px auto 20px;
    line-height: 78px;
    text-align: center;
    border: 1px solid #fff;
    border-radius: 50%;
}
.step .icon[data-v-0a95724c]:before {
    content: attr(data-step);
    position: absolute;
    top: -10px;
    left: 5px;
    display: block;
    width: 32px;
    height: 32px;
    line-height: 38px;
    background-color: #ffb42d;
    border-radius: 50%;
    font-size: 1.5em;
    font-family: "Give You Glory", cursive;
    font-weight: 700;
    color: #fff;
}
.step .icon img[data-v-0a95724c],
.step .icon svg[data-v-0a95724c] {
    display: inline-block;
    vertical-align: middle;
}
.how-it-works .step-2[data-v-0a95724c] {
    padding-top: 10%;
}
.how-it-works .step-3[data-v-0a95724c] {
    padding-top: 10%;
}
.col1[data-v-0a95724c]:after {
    position: absolute;
    content: "";
    right: -21%;
    top: 15%;
    width: 185px;
    height: 34px;
    background: url(../../../assets/img/home/arrow-big.png) no-repeat;
}
.col2[data-v-0a95724c]:after {
    position: absolute;
    content: "";
    right: -16%;
    top: 11%;
    width: 175px;
    height: 34px;
    background: url(../../../assets/img/home/arrow-big-dotted.png) no-repeat;
}
.how-it-works-solution[data-v-0a95724c] {
    margin-bottom: 26px;
}
@media (max-width: 543px) {
.col1[data-v-0a95724c]:after,
    .col2[data-v-0a95724c]:after {
        display: none;
}
.how-it-works h3[data-v-0a95724c] {
        font-size: 25px;
        text-align: center;
}
.how-it-works p[data-v-0a95724c] {
        font-weight: 300;
        text-align: center;
}
}
@media (min-width: 544px) and (max-width: 767px) {
.col1[data-v-0a95724c]:after,
    .col2[data-v-0a95724c]:after {
        display: none;
}
}
@media (min-width: 768px) and (max-width: 991px) {
.col1[data-v-0a95724c]:after,
    .col2[data-v-0a95724c]:after {
        display: none;
}
}
@media (min-width: 992px) and (max-width: 1199px) {
.col1[data-v-0a95724c]:after,
    .col2[data-v-0a95724c]:after {
        right: -30%;
}
}



